	height:40vh;
	border:2px solid black;
	border-radius:20px;
	margin-left:30vw;
	margin-top:30vh;
	position:relative;
	padding:50px;
	min-width:fit-content;

}


.login{
	font-size:90px;
	text-align:center;

}

.username{
	font-size:40px;
}

.username-input{
	font-size: 30px;
	padding:5px;
	width:60%;
}

.password{
	font-size:40px;
}
.password-input{
	padding:5px;
	font-size:30px;
	width:60%;
	margin-right:0px;
}

.email-input{
	padding:5px;
	font-size:30px;
	width:60%;
}

.email{
	font-size:40px;
}

.submit{
	width:200px;
	height:60px;
	border:dotted 3px black;
	background-color:rgba(1,1,1,0);
	font-size: 30px;
	font-weight:500;

}
