 

.header{
    position: fixed;
    top: 0px;
    width: 100vw;
    height:100px;
    background-color: rgba(43, 43, 43,0.7);  
    z-index: 2;
}

.header *{
    float: left;
}

.footer{  
    width: 100vw;
    height: 100px;
    background-color: white;
    bottom: 0px;
}
.body{
    height: calc(100vh - 100px);
    overflow-y: scroll;
    overflow-x: hidden; 
    width: calc(100vw - 100px); 
    margin-left: 50px;
    margin-right: 50px;
}

#search{
    width: 20vw;
    min-width: 300px;
    height: 50px; 
    border-radius: 20px;
    padding-left: 20px;
    font-size: 30px;
    margin-top:25px;
    margin-left:20vw;
    border: 1px solid black;



}

#search-span{
	position:relative;
}

#sug{
	position:absolute;
	width:100%;
	max-height:30vh;
	bottom:0px;
	left:0px;

}

.logo{
    width: 100px;
    height: 100px;
}

#message{
height: 80px;
margin-top: 10px;
width: 70vw;
border-radius: 30px;
margin-left: 10vw;
border: 1px solid black;
padding-left: 30px;
font-size: 30px;
}

#submit{
    width: 10vw;
    height: 80px;
    margin-top: 10px;
    border-radius: 30px;
    border: 1px solid black;
    float: right;
    margin-right: 10px;
}

.message-our{
    border: dotted 2px black;
    width: fit-content;
    height: fit-content;
    padding: 40px;
    margin-top: 20px;
    margin-bottom: 20px;
    border-radius: 30px;
    margin-left: 0px;
    margin-right: auto;
    background-color: lightpink;
    font-size: 30px;
    position: relative;
    
}

.message-your{
    position: relative;
    border: dotted 2px black;
    width: fit-content;
    height: fit-content;
    padding: 40px;
    margin-top: 20px;
    margin-bottom: 20px;
    border-radius: 30px;
    margin-right: 0px;
    margin-left: auto;
    background-color: lightblue;
    font-size: 30px; 
}

.author{
    position: absolute;
    top: 0px;
    left: 60px;
    font-size: 10px;
}

.date{
    position: absolute;
    top: 0px;
    right: 30px;
    font-size: 10px;
}

.profilepic-author{
    position: absolute;
    width: 30px;
    height: 30px;
    top: 0px;
    left: 30px;
}

.usrname{
    font-size: 50px;
    float: right;
    color: aliceblue;
}

.profile{
    height: 100px;
    width: 100px;
    float: right;
}

#create-center{
    width: 60vw;
    margin-left: 20vw;
    height: 60vh;
    margin-top: 20vh;
    border: 1px solid black; 
    font-size: 5vh;
}

#create-check{ 
    height: 5vh;
}

#create-pass{ 
    font-size: 5vh;
}

#create-sub{ 
    font-size: 5vh;
}